import React, { useEffect } from "react";
import "./productPage.css";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { useState } from "react";
import Products from "../../components/products/Products";
import Product from "../../components/product/Product";

export default function ProductPage() {
  const [products, setProducts] = useState([]);
  const { search } = useLocation();

  useEffect(() => {
    const fetchProducts = async () => {
      const res = await axios.get("/api/products" + search);
      setProducts(res.data);
      console.log(res);
    };
    fetchProducts();
  }, [search]);


  return (
    <div className="productPage">
      <div className="ppTop">
        <span>All Products</span>
        <div className="ppTopRight">
          <div className="searchBar">
            <i className="icon fa-solid fa-magnifying-glass"></i>
            <input type="text" placeholder="Search" />
          </div>
          <button className="filterBtn">
            <i className="fa-solid fa-filter"></i>
          </button>
        </div>
      </div>
      <div className="ppBottom">
        <div className="ppbCont">
          <span className="contName">HeadPhone</span>
          <div className="ppContBottom">
            <div className="holder">
              {/* <Products products={products} /> */}
              {products?.map((p) =>
                p.producttype == "Headphone" ? <Product product={p} /> : null
              )}
            </div>
          </div>
        </div>
        <div className="ppbCont">
          <span className="contName">EarBuds</span>
          <div className="ppContBottom">
            <div className="holder">
            {products?.map((p) =>
                p.producttype == "Earbuds" ? <Product product={p} /> : null
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
