import React from "react";
import Product from "../product/Product";
import "./products.css";

export default function Products({ products }) {
  function scrollLeft() {
    var left = document.querySelector(".allProduct");
    left.scrollBy(350, 0);
  }

  function scrollRight() {
    var left = document.querySelector(".allProduct");
    left.scrollBy(-350, 0);
  }

  return (
    <div className="products">
      <button className="scrollLeft" onClick={scrollLeft}>
        <i className="icon fa-solid fa-left-long"></i>
      </button>

      <div className="parent">
        <div className="allProduct">
          {products?.map((p) => (
            <Product product={p} />
          ))}

          {/* {products?.map((p) =>
            p.producttype == "Headphone" ? <Product product={p} /> : null
          )} */}

          {/* {products && products.producttype == "Headphone"
            ? products.map((p) => <Product product={p} />)
            : "No Products"} */}
        </div>
      </div>

      <button className="scrollRight" onClick={scrollRight}>
        <i className="icon fa-solid fa-right-long"></i>
      </button>
    </div>
  );
}
