import React from "react";
import { Link } from "react-router-dom";
import "./cartProduct.css";

export default function CartProduct({ cartProduct }) {
  return (
    <Link to={`/product/${cartProduct._id}`} className=" prodLink">
      <div className="cartProduct">
        <div className="cpLeft">
          <img src={cartProduct.imgCollection[0]} alt="" className="prodImg" />
        </div>
        <div className="cpRight">
          <span className="cpProdName">{cartProduct.name}</span>
          <span className="cpProdDesc">{cartProduct.name}</span>
          <div className="cpProdPrice">
            <span className="price">{cartProduct.price}</span>
            <span className="discount">{cartProduct.discount}</span>
            <span className="offer">{cartProduct.offer}</span>
          </div>
          <div className="cpProdQuant">
            <i className="icon fa-solid fa-plus"></i>
            <span className="totQuant">1</span>
            <i className="icon fa-solid fa-minus"></i>
          </div>
        </div>
      </div>
    </Link>
  );
}
